import React, {Component} from 'react' ;
//use grid system in react-mdl similar to bootstrap
import {Grid,Cell} from 'react-mdl';

class Landing extends Component {
  render(){
    return(
      <div style={{width: '100%', margin: 'auto'}}>
      <Grid className='landing-grid'>
      <Cell col={12}>
      <img src="https://image.flaticon.com/icons/svg/145/145867.svg"
      alt="my-avatar"
      class="avatar-image"/>
      <div className="banner-text">
      <h1>Computer Science Graduate</h1>
      <hr/>
      <p>HTML/CSS | Bootstrap | JavaScript | React | NodeJS | MySQL | MongoDB </p>
        
      <div className="social-links">

      <a href="https://linkedin.com/in/hongzhizhang/" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-linkedin-square" aria-hidden="true"/>
      </a>
      <a href="https://linkedin.com/in/hongzhizhang/" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-github-square" aria-hidden="true"/>
      </a>
      </div>
      </div>

      </Cell>
      </Grid>
      </div>
    )
  }
}

export default Landing;
