import React, { Component } from 'react';
import {Grid, Cell} from 'react-mdl';

class Skills extends Component{
  render(){
    return(
      <Grid>
      <Cell col={4}>
      <p>{this.props.skillName}</p>
      </Cell>
      <Cell col={8}>
      <p>{this.props.skillDescription}</p>
      </Cell>
      </Grid>
    )
  }
}

export default Skills;
