import React, {
  Component
} from 'react';
import {
  Grid,
  Cell,
  List,
  ListItem,
  ListItemContent
} from 'react-mdl';

class Contact extends Component {
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid" >
          <Cell col={6}>
            <h2> Hongzhi Zhang </h2> <img src="https://cdn2.iconfinder.com/data/icons/avatar-2/512/Fred_man-512.png" alt="avatar"
              style={{ height: '250px' }} />
            <p style={{
              width: '75px',
              margin: 'auto',
              paddingTop: '1em'
            }}>
              asdfasdfaf
          </p>
          </Cell>
          <Cell col={6}>
            <h2> Contact Me </h2>
            <hr />
            <div className="contact-list">
              <List>
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }} >
                    <i className="fa fa-phone-square" aria-hidden="true" />
                    +45 11111111 </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }} >
                    <i className="fa fa-envelope" aria-hidden="true" />
                    zhhzh2217@gmail.com </ListItemContent> </ListItem >
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }}>
                    <i className="fa fa-skype" aria-hidden="true" />
                    naobuzhugu </ListItemContent> </ListItem >
                <ListItem>
                  <ListItemContent style={{ fontSize: '30px', fontFamily: 'Anton' }}>
                    <i className="fa fa-linkedin-square" aria-hidden="true" />
                    https://www.linkedin.com/in/hongzhizhang/ </ListItemContent> </ListItem>
              </List>
            </div>
          </Cell>
        </Grid>
      </div >)
  }
}

export default Contact;
